body, ul {
  --active-color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  padding: 0 50px;
}

.container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.navigation__container {
  align-items: center;
  list-style: none;
  display: flex;
}

.navigation__item {
  align-items: center;
  margin-right: 20px;
  display: flex;
}

.navigation__item:last-child {
  margin: 0;
}

.navigation__item a {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}

a.is-active, .navigation__item a:hover {
  color: var(--active-color);
}

